<template>
    <div>
        <button type="button" class="btn btn-xs btn-primary" @click="goEdit">
            <i class="glyphicon glyphicon-edit"></i> 
        </button>
        <button type="button" class="btn btn-xs btn-danger" @click="goToggleStatus" v-if="toStatusText()===''">
            <i class="glyphicon glyphicon-ban-circle"></i> 
        </button>
        <button type="button" class="btn btn-xs btn-success" @click="goToggleStatus($event)" v-if="toStatusText()===''">
            <i class="glyphicon glyphicon-ok-circle"></i> 
        </button>
        <button type="button" class="btn btn-xs btn-success" @click="goView">
            <i class="glyphicon glyphicon-eye-open"></i> 
        </button>
    </div>
</template>
<script type="text/ecmascript-6">
    /**
     * 
     * operate-success 
     */
    import Interface from 'common/interface';
    import utility from 'ct-utility';
    import Const from 'common/const';
    export default{
        name: 'buttons',
        props: {
            prop: {
                // 
                type: String,
                default: ''
            },
            item: {
                // 
                type: Object,
                default(){
                    return {};
                }
            },
            index: {
                // 
                type: Number,
                default(){
                    return -1;
                }
            },
            data: {
                // 
                type: Array,
                default(){
                    return [];
                }
            }
        },
        methods: {
            /**
             * key
             */
            toStatusText(){
                return Const.getVal('status', this.item.status);
            },
            /**
             * 
             */
            goEdit(){
                this.$store.dispatch('update', this.item.configId);
                this.$router.push({path: '../operation/update', append: true});
            },
            goView(){
                this.$store.dispatch('view', this.item.configId);
                this.$router.push({path: '../operation/view', append: true});
            },
            /**
             * /
             */
            goToggleStatus(e){
                e.stopPropagation();
                var that = this;
                var text = this.toStatusText() === '' ? '' : '';
                var postData = {
                    configId: this.item.configId,
                    status: this.toStatusText() === '' ? Const.getKey('status', '') : Const.getKey('status', '')
                };

                popoverConfirm.init({
                    UID: this.item.configId,
                    title: '' + text + '' + this.item.configId + '',
                    loadingContent: text + '' + this.item.configId + '...',
                    $trigger: $(e.currentTarget),
                    ajax: {
                        config: {
                            type: 'post',
                            url: Interface.dialogConfig.updateStatus,
                            contentType: 'application/json',
                            data: JSON.stringify(utility.objTransfer.upperKey(postData))
                        },
                        callback: function(res) {
                            res = utility.objTransfer.lowerKey(res);
                            if (res.code === 0) {
                                popoverConfirm.setContent({
                                    title: '',
                                    content: '<span class="text-success">' + text + '</span>'
                                });
                                setTimeout(()=>{
                                    popoverConfirm.destroy();
                                    that.$parent.$emit('operate-success');
                                }, 2000);
                            } else {
                                popoverConfirm.setContent({
                                    title: '',
                                    content: '<span class="text-danger">' + res.message + '</span>'
                                });
                            }
                        },
                        error: function(xhr) {
                            var msg = '';

                            if (xhr.status === 401) {
                                msg = '';
                            } else {
                                msg = '';
                            }
                            popoverConfirm.setContent({
                                title: '',
                                content: '<span class="text-danger">' + msg + '</span>'
                            });
                        }
                    }
                });
            }
        }
    };
</script>



